export default {
    data: () => ({
        conceptText: "全体コンセプト\r\n全体コンセプト",
        philosophyDescription:
            "合同会社まちたびデザインは、\r\n代表が世界一周をして感じた\r\n日本の魅力・新潟の魅力(食・文化・景色等)を\r\n世界中を旅する人にもっと知ってもらいたい、\r\nもっと新潟に人を呼び込みたい、\r\n旅人たちの交流の場となり世界中を旅するきっかけになりたい、\r\n子どもたちが世界を身近に感じて世界へ興味を持つきっかけにしたい、\r\n新潟の魅力を知った人たちが帰国後に\r\n世界に羽ばたき発信する側となってもらいたい\r\n地元の人にももっと魅力を知ってもらいたい\r\nまちの魅力を高めたい\r\nそんな壮大な想いから\r\nゲストハウス×カフェ×まちづくりコンサルタント\r\nの3つの軸で活動できるように立ち上げました。\r\n旅人・地元の人の交流ができ、\r\n日常が旅をしているようなわくわくする感覚で魅力を発見できる\r\n新潟のまちづくりを目指しています。\r\nゲストハウス、カフェは物件を探し中です。\r\nご興味のある方はぜひ一度ご連絡ください。",
        news: null,
        targetPosEvents: null,
        targetPosAbout: null,
        targetPosContact: null,
        scrollheight: null,
        loading: true,

        scrollToName: null,
        ls_event_handle: null,
    }),

    async mounted() {
        // const response =
        await fetch(url)
            .then((res) => res.json())
            .then((res) => (this.news = res));
        // console.log(response);
        // console.log(this.news);
        window.addEventListener(
            "storage",
            this.storageEvent
        );

        setTimeout(() => {
            this.loading = false;
            this.scrollheight = window.scrollY;
            // + window.scrollYがない場合、読み込み時のスクロール状態によりずれが発生する
            this.targetPosEvents =
                this.$refs.events.getBoundingClientRect().top +
                this.scrollheight -
                window.innerHeight / 2;
            this.targetPosAbout =
                this.$refs.About.getBoundingClientRect().top +
                this.scrollheight -
                window.innerHeight / 2;
            this.targetPosContact =
                this.$refs.Contact.getBoundingClientRect().top +
                this.scrollheight -
                window.innerHeight / 2;

            this.scrollToName = JSON.parse(
                sessionStorage.getItem("ScrollToName")
            );
            if (this.scrollToName != null && this.scrollToName != undefined) {
                this.scrollToElement(this.scrollToName);
                sessionStorage.removeItem("ScrollToName");
            }
        }, 400);
    },

    destroyed() {
        window.removeEventListener("scroll", this.handleScroll);
    },

    methods: {
        // 特定の位置までスクロール
        scrollToElement(position) {
            // console.log(this.targetPosEvents);
            window.scrollTo({
                top: this[position] - 30 + window.innerHeight / 2,
                behavior: "smooth",
            });
        },

        storageEvent() {
            alert();
            this.scrollToName = JSON.parse(
                sessionStorage.getItem("ScrollToName")
            );
            if (this.scrollToName != null && this.scrollToName != undefined) {
                this.scrollToElement(this.scrollToName);
                sessionStorage.removeItem("ScrollToName");
            }
        },
    },
};
</script>
